var StartActionBuilder = require('./start')

/**
 * This class will build request, manipulate flags and chronos for the associated event.
 *
 * @constructs InitActionBuilder
 * @extends StartActionBuilder
 * @memberof youbora
 * @abstract
 */
var InitActionBuilder = StartActionBuilder.extend(
  /** @lends InitActionBuilder.prototype */
  {
    /**
     *update chronos and flags as needed.
     * ie: this._info.plugin.chronos.joinDuration.start()
     */
    setChronosAndFlags: function () {
      this._flags.isInitiated = true
    },

    /**
     * Return the service name
     *
     * @returns {string}
     */
    getServiceName: function () {
      return '/init'
    }
  }
)

module.exports = InitActionBuilder
