var ActionBuilder = require('./actionbuilder')
var Log = require('../log')

/**
 * This class will build request, manipulate flags and chronos for the associated event.
 *
 * @constructs AdJoinActionBuilder
 * @extends ActionBuilder
 * @memberof youbora
 * @abstract
 */
var AdJoinActionBuilder = ActionBuilder.extend(
  /** @lends AdJoinActionBuilder.prototype */
  {

    /**
     * Check the associated flags. ie: return this._info.getIsAdJoinSent()
     *
     * @returns {boolean}
     */
    getIsAllowed: function () {
      return !this._flags.isHalted &&
        this._adFlags.isStarted &&
        !this._adFlags.isJoined
    },

    /**
     *update chronos and flags as needed.
     * ie: this._info.plugin.chronos.total.start()
     */
    setChronosAndFlags: function () {
      this._adFlags.isJoined = true

      this._adChronos.join.stop()
    },

    /**
     * Return the service name
     *
     * @returns {string}
     */
    getServiceName: function () {
      return '/adJoin'
    },

    /**
     * Logs successful request.
     *
     * @param {object} params Object of params created.
     */
    notice: function (params) {
      Log.notice(this.getServiceName() + ' ' + params.joinDuration + 'ms')
    },

    /**
     * Return the params needed for the request.
     *
     * @returns {object}
     */
    getParams: function (params) {
      params = this._buildParams(params, ['joinAdDuration', 'adPlayhead'])
      params = this._buildParamsIfDifferent(params, [
        'adTitle',
        'adDuration',
        'adResource'
      ])
      params.adNumber = this._info.lastSent.adNumber
      return params
    }
  }
)

module.exports = AdJoinActionBuilder
