var ActionBuilder = require('./actionbuilder')
var Log = require('../log')

/**
 * This class will build request, manipulate flags and chronos for the associated event.
 *
 * @constructs PingActionBuilder
 * @extends ActionBuilder
 * @memberof youbora
 * @abstract
 */
var PingActionBuilder = ActionBuilder.extend(
  /** @lends PingActionBuilder.prototype */
  {
    /**
     * Check the associated flags. ie: return this._info.getIsPingSent()
     *
     * @returns {boolean}
     */
    getIsAllowed: function () {
      return !this._flags.isHalted &&
        this._flags.isStarted
    },

    /**
     * Return the service name
     *
     * @returns {string}
     */
    getServiceName: function () {
      return '/ping'
    },

    /**
     * Logs successful request.
     */
    notice: function (params) {
      Log.verbose(this.getServiceName())
    },

    /**
     * Return the params needed for the request.
     *
     * @returns {object}
     */
    getParams: function (params) {
      params = this._buildParams(params, [
        'playhead',
        'bitrate',
        'throughput',
        'fps',
        'droppedFrames',
        'playrate',

        'adPlayhead',
        'adBitrate'
      ])

      if (this._flags.isBuffering) params = this._buildParams(params, ['bufferDuration'])
      if (this._flags.isPaused) params = this._buildParams(params, ['pauseDuration'])
      if (this._flags.isSeeking) params = this._buildParams(params, ['seekDuration'])
      if (this._flags.isAdBuffering) params = this._buildParams(params, ['adBufferDuration'])

      params.entities = this._getChangedEntities()

      return params
    },

    /**
     * Return changed entities since last check
     *
     * @private
     */
    _getChangedEntities: function () {
      return this._buildParamsIfDifferent({}, [
        'rendition',
        'title',
        'title2',
        'live',
        'mediaDuration',
        'mediaResource',
        'param1',
        'param2',
        'param3',
        'param4',
        'param5',
        'param6',
        'param7',
        'param8',
        'param9',
        'param10',
        'adTitle',
        'connectionType',
        'deviceCode',
        'ip',
        'username',
        'cdn',
        'nodeHost',
        'nodeType',
        'nodeTypeString'
      ])
    }
  }
)

module.exports = PingActionBuilder
