var ActionBuilder = require('./actionbuilder')
var Log = require('../log')

/**
 * This class will build request, manipulate flags and chronos for the associated event.
 *
 * @constructs StartActionBuilder
 * @extends ActionBuilder
 * @memberof youbora
 * @abstract
 */
var StartActionBuilder = ActionBuilder.extend(
  /** @lends StartActionBuilder.prototype */
  {

    /**
     * Check the associated flags. ie: return this._info.getIsStartSent()
     *
     * @returns {boolean}
     */
    getIsAllowed: function () {
      return !this._flags.isStarted
    },

    /**
     *update chronos and flags as needed.
     * ie: this._info.plugin.chronos.joinDuration.start()
     */
    setChronosAndFlags: function () {
      this._flags.isStarted = true
      this._flags.isHalted = false

      if (this._chronos.join.startTime === 0) this._chronos.join.start()
      this._chronos.total.start()

      if (this._monitor) this._monitor.start()
    },

    /**
     * Return the service name
     *
     * @returns {string}
     */
    getServiceName: function () {
      return '/start'
    },

    /**
     * Logs successful request.
     *
     * @param {object} params Object of params created.
     */
    notice: function (params) {
      Log.notice(this.getServiceName() + ' ' + (params.title || params.mediaResource))
    },

    /**
     * Return the params needed for the request.
     *
     * @returns {object}
     */
    getParams: function (params) {
      return this._buildParams(params, [
        'accountCode',
        'username',
        'rendition',
        'title',
        'title2',
        'live',
        'mediaDuration',
        'mediaResource',
        'transactionCode',
        'properties',
        'cdn',
        'playerVersion',
        'param1',
        'param2',
        'param3',
        'param4',
        'param5',
        'param6',
        'param7',
        'param8',
        'param9',
        'param10',
        'pluginVersion',
        'pluginInfo',
        'isp',
        'connectionType',
        'ip',
        'deviceCode',
        'preloadDuration'
      ])
    }
  }
)

module.exports = StartActionBuilder
