var ActionBuilder = require('./actionbuilder')

/**
 * This class will build request, manipulate flags and chronos for the associated event.
 *
 * @constructs PreloadEndActionBuilder
 * @extends ActionBuilder
 * @memberof youbora
 * @abstract
 */
var PreloadEndActionBuilder = ActionBuilder.extend(
  /** @lends PreloadEndActionBuilder.prototype */
  {
    /**
     * Check the associated flags. ie: return this._info.getIsPreloadEndSent()
     *
     * @returns {boolean}
     */
    getIsAllowed: function () {
      return this._flags.isPreloading
    },

    /**
     *update chronos and flags as needed.
     * ie: this._info.plugin.chronos.total.start()
     */
    setChronosAndFlags: function () {
      if (this._flags.isPreloading) {
        this._flags.isPreloading = false

        this._chronos.preload.stop()
      }
    },

    /**
     * Return the service name
     *
     * @returns {string}
     */
    getServiceName: function () {
      return '/preload-end ' + this._chronos.preload.getDeltaTime(false) + 'ms'
    }
  }
)

module.exports = PreloadEndActionBuilder
