var ActionBuilder = require('./actionbuilder')
var Log = require('../log')

/**
 * This class will build request, manipulate flags and chronos for the associated event.
 *
 * @constructs AdBufferEndActionBuilder
 * @extends ActionBuilder
 * @memberof youbora
 * @abstract
 */
var AdBufferEndActionBuilder = ActionBuilder.extend(
  /** @lends AdBufferEndActionBuilder.prototype */
  {

    /**
     * Check the associated flags. ie: return this._info.getIsAdBufferEndSent()
     *
     * @returns {boolean}
     */
    getIsAllowed: function () {
      return !this._flags.isHalted &&
        this._adFlags.isJoined &&
        this._adFlags.isBuffering
    },

    /**
     *update chronos and flags as needed.
     * ie: this._info.plugin.chronos.total.start()
     */
    setChronosAndFlags: function () {
      this._adFlags.isBuffering = false

      this._adChronos.buffer.stop()
    },

    /**
     * Return the service name
     *
     * @returns {string}
     */
    getServiceName: function () {
      return '/adBufferUnderrun'
    },

    /**
     * Logs successful request.
     *
     * @param {object} params Object of params created.
     */
    notice: function (params) {
      Log.notice(this.getServiceName() + ' ' + params.adBufferDuration + 'ms')
    },

    /**
     * Return the params needed for the request.
     *
     * @returns {object}
     */
    getParams: function (params) {
      params = this._buildParams(params, ['adBufferDuration', 'adPlayhead', 'adPosition'])
      params.adNumber = this._info.lastSent.adNumber
      return params
    }
  }
)

module.exports = AdBufferEndActionBuilder
