var Emitter = require('../emitter')

var Transform = Emitter.extend(
  /** @lends Parser.prototype */

  /**
   * Transform classes among youbora helps the library parse and work with data.
   * HLSParser, CDNParser, ResourceTransform or ViewTransform all extend from this class.
   *
   * @constructs Parser
   * @extends Emitter
   * @memberof youbora
   * @abstract
   */
  {
    constructor: function () {
      this._isBusy = true
    },

    /**
     * Override this method to transform info.
     */
    parse: function () {},

    /**
     * Returns if transform is blocking
     * @param {YBRequest} request Request to transform.
     * @return {bool} True if queue shall be blocked.
     */
    isBlocking: function (request) {
      return this._isBusy
    },

    /**
     * Emits DONE event
     */
    done: function () {
      this._isBusy = false
      this.emit(Transform.Event.DONE)
    }
  },

  /** @lends Parser */
  {
    // Static members

    /**
     * List of events that could be fired from this class.
     * @enum
     */
    Event: {
      /**
       * Notifies that this parser is done processing.
       */
      DONE: 'done'
    }
  }
)

module.exports = Transform
