var YouboraObject = require('../object')
var Timer = require('../timer')
var Log = require('../log')

var Communication = require('../comm/communication')
var InfoGatherer = require('./infogatherer')
var ViewTransform = require('../transform/view')
var Nqs6Transform = require('../transform/nqs6')
var ResourceTransform = require('../transform/resource')

var PingActionBuilder = require('../actionbuilder/ping')
var PreloadBeginActionBuilder = require('../actionbuilder/preloadbegin')
var PreloadEndActionBuilder = require('../actionbuilder/preloadend')
var InitActionBuilder = require('../actionbuilder/init')
var StartActionBuilder = require('../actionbuilder/start')
var JoinActionBuilder = require('../actionbuilder/join')
var PauseActionBuilder = require('../actionbuilder/pause')
var ResumeActionBuilder = require('../actionbuilder/resume')
var SeekBeginActionBuilder = require('../actionbuilder/seekbegin')
var SeekEndActionBuilder = require('../actionbuilder/seekend')
var BufferBeginActionBuilder = require('../actionbuilder/bufferbegin')
var BufferEndActionBuilder = require('../actionbuilder/bufferend')
var StopActionBuilder = require('../actionbuilder/stop')
var ErrorActionBuilder = require('../actionbuilder/error')
var FatalErrorActionBuilder = require('../actionbuilder/fatalerror')
var AdStartActionBuilder = require('../actionbuilder/adstart')
var AdJoinActionBuilder = require('../actionbuilder/adjoin')
var AdPauseActionBuilder = require('../actionbuilder/adpause')
var AdResumeActionBuilder = require('../actionbuilder/adresume')
var AdBufferBeginActionBuilder = require('../actionbuilder/adbufferbegin')
var AdBufferEndActionBuilder = require('../actionbuilder/adbufferend')
var AdStopActionBuilder = require('../actionbuilder/adstop')

var YouboraController = YouboraObject.extend(
  /** @lends YouboraController.prototype */
  {
    /**
     * This class will manage the main flow of the application.
     *
     * @constructs YouboraController
     * @extends YouboraObject
     * @memberof youbora
     *
     * @param {GenericPlugin} plugin A GenericPlugin instance
     * @param {Options} options An Options instance
     */
    constructor: function (plugin, options) {
      /** A reference to the plugin */
      this.plugin = plugin

      /** a reference to options object */
      this.options = options

      // Adds plugin to static map
      YouboraController.map.push(plugin)

      this._init()

      this._registerListeners()
    },

    /**
     * Sets the {@link Options}.
     *
     * @param {Object|bool} [options={}] An object compiling to {@link Options} constructor.
     */
    setOptions: function (options) {
      this.options.setOptions(options)
    },

    /**
     * Get the current options.
     *
     * @returns {Options}
     */
    getOptions: function () {
      return this.options
    },

    /**
     * Init class properties
     */
    _init: function () {
      this._info = new InfoGatherer(this)
      this._ping = this._ping = new Timer(this._sendPing.bind(this), 5000)

      this.viewTransform = new ViewTransform(this._info)
      this.viewTransform.on(ViewTransform.Event.DONE, function (e) { // Data response
        this._ping.interval = e.target.response.pingTime * 1000
      }.bind(this))

      this.resourceTransform = new ResourceTransform(this.options, this._info)
    },

    /**
     * Reset all variables and stop all timers
     */
    reset: function () {
      this.stopPings()
      this.resourceTransform = new ResourceTransform(this.options, this._info)
    },

    /**
     * Initializes {@Communication}. Called in every /start.
     * @private
     */
    _initComm: function () {
      this.resourceTransform.init(this._info.getResource())

      this._comm = new Communication()
      this._comm.addTransform(this.viewTransform)
      this._comm.addTransform(this.resourceTransform)
      this._comm.addTransform(new Nqs6Transform())
    },

    /**
     * Starts sending pings
     */
    startPings: function () {
      if (!this._ping.isRunning) this._ping.start()
    },

    /**
     * Stops sending pings
     */
    stopPings: function () {
      this._ping.stop()
    },

    /**
     * Set all the plugin listeners
     * @private
     */
    _registerListeners: function () {
      this.plugin.on('init', this._initListener.bind(this))
      this.plugin.on('start', this._startListener.bind(this))
      this.plugin.on('join', this._joinListener.bind(this))
      this.plugin.on('pause', this._pauseListener.bind(this))
      this.plugin.on('resume', this._resumeListener.bind(this))
      this.plugin.on('seek-begin', this._seekBeginListener.bind(this))
      this.plugin.on('seek-end', this._seekEndListener.bind(this))
      this.plugin.on('buffer-begin', this._bufferBeginListener.bind(this))
      this.plugin.on('buffer-end', this._bufferEndListener.bind(this))
      this.plugin.on('error', this._errorListener.bind(this))
      this.plugin.on('fatal-error', this._fatalErrorListener.bind(this))
      this.plugin.on('stop', this._stopListener.bind(this))

      this.plugin.on('preload-begin', this._preloadBeginListener.bind(this))
      this.plugin.on('preload-end', this._preloadEndListener.bind(this))
    },

    /**
     * Sets an adnalyer and register its listeners. Will be called automatically.
     */
    setAdnalyzer: function () {
      if (this.plugin.adnalyzer) {
        this._registerAdnalyzerListeners()
      } else {
        Log.warn('Tried to call controller.setAdnalyzer without an adnalyzed defined.')
      }
    },

    _registerAdnalyzerListeners: function () {
      this.plugin.adnalyzer.on('start', this._adStartListener.bind(this))
      this.plugin.adnalyzer.on('join', this._adJoinListener.bind(this))
      this.plugin.adnalyzer.on('pause', this._adPauseListener.bind(this))
      this.plugin.adnalyzer.on('resume', this._adResumeListener.bind(this))
      this.plugin.adnalyzer.on('buffer-begin', this._adBufferBeginListener.bind(this))
      this.plugin.adnalyzer.on('buffer-end', this._adBufferEndListener.bind(this))
      this.plugin.adnalyzer.on('stop', this._adStopListener.bind(this))
    },

    /**
     * Send request to {@link Communication}
     *
     * @private
     * @param {Object} event Event data
     * @param {ActionBuilder} builder Builder object
     * @param {boolean} [adnalyer=true] If true, pre-event will be emitted from the adnalyzer.
     */
    _sendRequest: function (event, request, adnalyzer) {
      if (request && this.options['config.enabled']) {
        if (adnalyzer && this.plugin.adnalyzer) {
          this.plugin.adnalyzer.emit('pre-' + event.type)
        } else {
          this.plugin.emit('pre-' + event.type)
        }
        this._comm.sendRequest(request)
      }
    },

    /**
     * Set flags and notice
     *
     * @private
     * @param {Object} event Event data
     * @param {ActionBuilder} builder Builder object
     * @param {boolean} [adnalyer=true] If true, pre-event will be emitted from the adnalyzer.
     */
    _executeBuilder: function (event, builder, adnalyzer) {
      if (builder.getIsAllowed()) {
        builder.setChronosAndFlags(event)
        builder.notice()
        if (adnalyzer && this.plugin.adnalyzer) {
          this.plugin.adnalyzer.emit('pre-' + event.type)
        } else {
          this.plugin.emit('pre-' + event.type)
        }
      }
    },

    _preloadBeginListener: function (e) {
      this._executeBuilder(e, new PreloadBeginActionBuilder(this._info))
    },

    _preloadEndListener: function (e) {
      this._executeBuilder(e, new PreloadEndActionBuilder(this._info))
    },

    _initListener: function (e) {
      var builder = new InitActionBuilder(this._info)
      if (builder.getIsAllowed()) {
        this.viewTransform.nextView()
        this._initComm()
        this.startPings()
      }

      this._sendRequest(e, builder.buildRequest())
    },

    _startListener: function (e) {
      var builder = new StartActionBuilder(this._info)
      if (builder.getIsAllowed()) {
        if (!this.plugin.flags.isInitiated) this.viewTransform.nextView()
        if (!this._comm) this._initComm()
        this.startPings()
      }

      this._sendRequest(e, builder.buildRequest())
    },

    _joinListener: function (e) {
      this._sendRequest(e, new JoinActionBuilder(this._info).buildRequest())
    },

    _pauseListener: function (e) {
      this._sendRequest(e, new PauseActionBuilder(this._info).buildRequest())
    },

    _resumeListener: function (e) {
      this._sendRequest(e, new ResumeActionBuilder(this._info).buildRequest())
    },

    _seekBeginListener: function (e) {
      this._executeBuilder(e, new SeekBeginActionBuilder(this._info))
    },

    _seekEndListener: function (e) {
      this._sendRequest(e, new SeekEndActionBuilder(this._info).buildRequest())
    },

    _bufferBeginListener: function (e) {
      this._executeBuilder(e, new BufferBeginActionBuilder(this._info))
    },

    _bufferEndListener: function (e) {
      this._sendRequest(e, new BufferEndActionBuilder(this._info).buildRequest())
    },

    _errorListener: function (e) {
      if (!this._comm) this._initComm()
      this._sendRequest(e, new ErrorActionBuilder(this._info).buildRequest())
    },

    _fatalErrorListener: function (e) {
      if (!this._comm) this._initComm()
      this._sendRequest(e, new FatalErrorActionBuilder(this._info).buildRequest())
      this.reset()
    },

    _stopListener: function (e) {
      this._sendRequest(e, new StopActionBuilder(this._info).buildRequest())
      this.reset()
    },

    // Ads
    _adStartListener: function (e) {
      this._sendRequest(e, new AdStartActionBuilder(this._info).buildRequest(), true)
    },

    _adJoinListener: function (e) {
      this._sendRequest(e, new AdJoinActionBuilder(this._info).buildRequest(), true)
    },

    _adPauseListener: function (e) {
      this._sendRequest(e, new AdPauseActionBuilder(this._info).buildRequest(), true)
    },

    _adResumeListener: function (e) {
      this._sendRequest(e, new AdResumeActionBuilder(this._info).buildRequest(), true)
    },

    _adBufferBeginListener: function (e) {
      this._executeBuilder(e, new AdBufferBeginActionBuilder(this._info), true)
    },

    _adBufferEndListener: function (e) {
      this._sendRequest(e, new AdBufferEndActionBuilder(this._info).buildRequest(), true)
    },

    _adStopListener: function (e) {
      this._sendRequest(e, new AdStopActionBuilder(this._info).buildRequest(), true)
    },

    _sendPing: function (diffTime) {
      if (this.options['config.enabled']) {
        var builder = new PingActionBuilder(this._info)
        this._comm.sendRequest(builder.buildRequest({ diffTime: diffTime }))
      }
    }
  },
  /** @lends YouboraController */
  {
    // Static members
    /** Contains a static reference to plugins created. */
    map: []
  }
)

module.exports = YouboraController
