var ErrorActionBuilder = require('./error')
var Log = require('../log')

/**
 * This class will build request, manipulate flags and chronos for the associated event.
 *
 * @constructs FatalErrorActionBuilder
 * @extends ErrorActionBuilder
 * @memberof youbora
 * @abstract
 */
var FatalErrorActionBuilder = ErrorActionBuilder.extend(
  /** @lends FatalErrorActionBuilder.prototype */
  {
    /**
     *update chronos and flags as needed.
     * ie: this._info.plugin.chronos.joinDuration.start()
     */
    setChronosAndFlags: function () {
      this._flags.reset()
      this._adFlags.reset()
      this._chronos.reset()
      this._adChronos.reset()

      this._flags.isHalted = true
    },

    /**
     * Logs successful request.
     *
     * @param {object} params Object of params created.
     */
    notice: function (params) {
      Log.notice(this.getServiceName() + ' FATAL ' + params.errorMsg)
    },

    /**
     * Return the params needed for the request.
     *
     * @returns {object}
     */
    getParams: function (params) {
      params = ErrorActionBuilder.prototype.getParams.apply(this, params)
      params.errorLevel = 'fatal'
      return params
    }
  }
)

module.exports = FatalErrorActionBuilder
