var Emitter = require('../emitter')
var Options = require('../options')
var Controller = require('../controller/controller')
var Log = require('../log')
var version = require('../version')
var ChronoStatus = require('./chronostatus')
var FlagStatus = require('./flagstatus')
var PlayheadMonitor = require('./playheadmonitor')

var GenericPlugin = Emitter.extend(
  /** @lends GenericPlugin.prototype */
  {
    /**
     * This class is the generic plugin from which specifics plugins will extend.
     *
     * @constructs GenericPlugin
     * @extends Emitter
     * @memberof youbora
     *
     * @param {Object|bool} [options={}]
     * An object compiling to {@link Options} constructor.
     * If you send a true, the plugin will be created without creating its {@link Controller},
     * this is useful for testing and creating Adnalyzers.
     */
    constructor: function (options) {
      /** An instance of {@link FlagStatus} */
      this.flags = new FlagStatus()

      /** An instance of {@link ChronoStatus} */
      this.chronos = new ChronoStatus()

      if (options !== true) {
        /** Reference to the player tag */
        this.player = null

        /** Reference to the video/object tag, could be the same as the player. */
        this.tag = null

        /**
         * Reference to an instance of {@link GenericPlugin} that will work as an ad analyzer.
         * @type GenericPlugin
         */
        this.adnalyzer = null

        /**
         * Instance of {@link Controller}, unless true is sent in constructor.
         * WARNING: Only access this if you know what you are doing!
         * @type Controller
         */
        this.controller = new Controller(this, new Options(options))

        Log.notice('Plugin ' + this.getVersion() + ' with Lib ' + version + ' is ready.')
      }
    },

    /**
     * Begin monitoring the given player. If it is an object, it will be saved as this.player.
     * It it is a string, it will be saved as this.player = document.getElementById(...)
     * Calls this.registerListeners.
     *
     * @param {object|string} player Either an instance of the player or a string containing an ID.
     */
    beginMonitoring: function (player) {
      if (typeof player === 'string') {
        this.player = document.getElementById(player)
      } else {
        this.player = player
      }

      this.tag = this.player

      this.resumeMonitoring()
    },

    /**
     * Calls registerListeners if exists. Resume pings.
     */
    resumeMonitoring: function () {
      if (this.controller && this.flags.isStarted) this.controller.startPings()
      if (this.registerListeners) this.registerListeners()
    },

    /**
     * Calls unregisterListeners. Pauses pings.
     */
    pauseMonitoring: function () {
      if (this.controller && this.flags.isStarted) this.controller.stopPings()

      if (this.unregisterListeners) this.unregisterListeners()
    },

    /**
     * Removes this.player and this.tag references. Calls this.unregisterListeners.
     */
    endMonitoring: function () {
      this.pauseMonitoring()

      this.player = null
      this.tag = null

      if (this.controller) this.controller.reset()
    },

    /**
     * Override to create event binders.
     * @example
     * registerListeners: function () {
     *  this.player.addEventListener('start', this.onStart.bind(this))
     * },
     * onStart: function (e) {
     *  this.emit('start')
     * }
     */
    registerListeners: function () {},

    /**
     * Override to create event de-binders.
     * @example
     * registerListeners: function () {
     *  this.player.removeEventListener('start', this.onStart)
     * }
     */
    unregisterListeners: function () {},

    /** Override to return current playhead of the video */
    getPlayhead: function () { return 0 },

    /** Override to return current playrate */
    getPlayrate: function () { return !this.flags.isPaused ? 1 : 0 },

    /** Override to return Frames Per Secon (FPS) */
    getFramesPerSecond: function () { return null },

    /** Override to return dropped frames since start */
    getDroppedFrames: function () { return null },

    /** Override to return video duration */
    getDuration: function () { return 0 },

    /** Override to return current bitrate */
    getBitrate: function () { return -1 },

    /** Override to return user bandwidth throughput */
    getThroughput: function () { return -1 },

    /** Override to return rendition */
    getRendition: function () { return '' },

    /** Override to return title */
    getTitle: function () { return '' },

    /** Override to return title2 */
    getTitle2: function () { return '' },

    /** Override to recurn true if live and false if VOD */
    getIsLive: function () { return false },

    /** Override to return resource URL. */
    getResource: function () { return 'unknown' },

    /** Override to return player version */
    getPlayerVersion: function () { return null },

    /** Override to return player's name */
    getPlayerName: function () { return null },

    /** Override to return plugin version. */
    getVersion: function () { return '6.0.0-Generic' },

    /** Override to return current ad position (only ads) */
    getPosition: function () { return null },

    /**
     * Sets an adnalyzer instance.
     *
     * @param {GenericPlguin} adnalyer Adnalyzer to be set.
     */
    setAdnalyzer: function (adnalyzer) {
      this.adnalyzer = adnalyzer
      if (this.controller) this.controller.setAdnalyzer()
    },

    /**
     * Sets the plugin {@link Options}.
     *
     * @param {Object|bool} [options={}] An object compiling to {@link Options} constructor.
     */
    setOptions: function (options) {
      if (this.controller) {
        this.controller.setOptions(options)
      } else {
        Log.warn('Tried to call setOptions in a plugin without controller.')
      }
    },

    /**
     * Get the current options.
     *
     * @returns {Options}
     */
    getOptions: function () {
      if (this.controller) {
        return this.controller.getOptions()
      } else {
        Log.warn('Tried to call getOptions in a plugin without controller.')
      }
    },

    /**
     * Creates a {@link PlayheadMonitor} in this.monitor. It will track buffers.
     */
    createBufferMonitor: function () {
      this.monitor = new PlayheadMonitor(this, PlayheadMonitor.Type.BUFFER)
    },

    /**
     * Creates a {@link PlayheadMonitor} in this.monitor. It will track seeks.
     */
    createSeekMonitor: function () {
      this.monitor = new PlayheadMonitor(this, PlayheadMonitor.Type.SEEK)
    },

    /**
     * Creates a {@link PlayheadMonitor} in this.monitor. It will track buffers and seeks.
     */
    createBufferAndSeekMonitor: function () {
      this.monitor = new PlayheadMonitor(this,
        PlayheadMonitor.Type.SEEK | PlayheadMonitor.Type.BUFFER
      )
    }
  },
  /** @lend GenericPlugin */
  {
    // Static Memebers
    /**
     * List of events that could be fired
     * @enum
     */
    Event: {
      INIT: 'init',
      START: 'start',
      JOIN: 'join',
      PAUSE: 'pause',
      RESUME: 'resume',
      SEEK_BEGIN: 'seek-begin',
      SEEK_END: 'seek-end',
      BUFFER_BEGIN: 'buffer-begin',
      BUFFER_END: 'buffer-end',
      ERROR: 'error',
      FATAL_ERROR: 'fatal-error',
      STOP: 'stop',
      PRELOAD_BEGIN: 'preload-begin',
      PRELOAD_END: 'preload-end',

      PRE_START: 'pre-start',
      PRE_JOIN: 'pre-join',
      PRE_PAUSE: 'pre-pause',
      PRE_RESUME: 'pre-resume',
      PRE_SEEK_BEGIN: 'pre-seek-begin',
      PRE_SEEK_END: 'pre-seek-end',
      PRE_BUFFER_BEGIN: 'pre-buffer-begin',
      PRE_BUFFER_END: 'pre-buffer-end',
      PRE_ERROR: 'pre-error',
      PRE_FATAL_ERROR: 'pre-fatal-error',
      PRE_STOP: 'pre-stop',
      PRE_PRELOAD_BEGIN: 'pre-preload-begin',
      PRE_PRELOAD_END: 'pre-preload-end'
    }
  }
)

module.exports = GenericPlugin
