var ActionBuilder = require('./actionbuilder')
var Log = require('../log')

/**
 * This class will build request, manipulate flags and chronos for the associated event.
 *
 * @constructs AdStopActionBuilder
 * @extends ActionBuilder
 * @memberof youbora
 * @abstract
 */
var AdStopActionBuilder = ActionBuilder.extend(
  /** @lends AdStopActionBuilder.prototype */
  {

    /**
     * Check the associated flags. ie: return this._info.getIsAdStopSent()
     *
     * @returns {boolean}
     */
    getIsAllowed: function () {
      return !this._flags.isHalted &&
        this._adFlags.isStarted
    },

    /**
     *update chronos and flags as needed.
     * ie: this._info.plugin.chronos.total.start()
     */
    setChronosAndFlags: function () {
      var chrono = this._adChronos.total
      chrono.stop()

      this._adFlags.reset()
      this._adChronos.reset()

      this._adChronos.total = chrono

      if (this._adMonitor) this._adMonitor.stop()
    },

    /**
     * Return the service name
     *
     * @returns {string}
     */
    getServiceName: function () {
      return '/adStop'
    },

    /**
     * Logs successful request.
     *
     * @param {object} params Object of params created.
     */
    notice: function (params) {
      Log.notice(this.getServiceName() + ' ' + params.totalAdDuration + 'ms')
    },

    /**
     * Return the params needed for the request.
     *
     * @returns {object}
     */
    getParams: function (params) {
      var params = this._buildParams(params, [
        'adBitrate',
        'adPlayhead',
        'totalAdDuration',
        'adPosition'
      ])
      params.adNumber = this._info.lastSent.adNumber
      return params
    }
  }
)

module.exports = AdStopActionBuilder
