var ActionBuilder = require('./actionbuilder')
var Log = require('../log')

/**
 * This class will build request, manipulate flags and chronos for the associated event.
 *
 * @constructs AdStartActionBuilder
 * @extends ActionBuilder
 * @memberof youbora
 * @abstract
 */
var AdStartActionBuilder = ActionBuilder.extend(
  /** @lends AdStartActionBuilder.prototype */
  {

    /**
     * Check the associated flags. ie: return this._info.getIsAdStartSent()
     *
     * @returns {boolean}
     */
    getIsAllowed: function () {
      return !this._flags.isHalted &&
        this._flags.isStarted &&
        !this._adFlags.isStarted
    },

    /**
     *update chronos and flags as needed.
     * ie: this._info.plugin.chronos.total.start()
     */
    setChronosAndFlags: function () {
      this._adFlags.isStarted = true

      this._adChronos.join.start()
      this._adChronos.total.start()

      if (this._adMonitor) this._adMonitor.start()
    },

    /**
     * Return the service name
     *
     * @returns {string}
     */
    getServiceName: function () {
      return '/adStart'
    },

    /**
     * Logs successful request.
     *
     * @param {object} params Object of params created.
     */
    notice: function (params) {
      Log.notice(this.getServiceName() + ' ' + (params.adTitle || params.adResource))
    },

    /**
     * Return the params needed for the request.
     *
     * @returns {object}
     */
    getParams: function (params) {
      // We need to get this before buildParams is called or adNumber will be overrided
      var adNumber = this._getAdNumber()

      params = this._buildParams(params, [
        'playhead',
        'adTitle',
        'adPosition',
        'adDuration',
        'adResource',
        'adPlayerVersion',
        'adProperties',

        'adnalyzerVersion'
      ])
      params.adNumber = adNumber

      return params
    },

    _getAdNumber: function () {
      var adNumber = this._info.lastSent.adNumber
      if (adNumber && this._info.lastSent.adPosition === this._info.getAdPosition()) {
        adNumber += 1
      } else {
        adNumber = 1
      }
      this._info.lastSent.adNumber = adNumber
      return adNumber
    }
  }
)

module.exports = AdStartActionBuilder
