var ActionBuilder = require('./actionbuilder')
var Log = require('../log')

/**
 * This class will build request, manipulate flags and chronos for the associated event.
 *
 * @constructs BufferBeginActionBuilder
 * @extends ActionBuilder
 * @memberof youbora
 * @abstract
 */
var BufferBeginActionBuilder = ActionBuilder.extend(
  /** @lends BufferBeginActionBuilder.prototype */
  {

    /**
     * Check the associated flags. ie: return this._info.getIsBufferBeginSent()
     *
     * @returns {boolean}
     */
    getIsAllowed: function () {
      return !this._flags.isHalted &&
        this._flags.isJoined &&
        !this._flags.isBuffering
    },

    /**
     *update chronos and flags as needed.
     * Will convert from seek if e.data.convertFromSeek is true.
     * ie: this._info.plugin.chronos.total.start()
     *
     * @param {Object} [e] The event info
     */
    setChronosAndFlags: function (e) {
      if (e.data && e.data.convertFromSeek === true && this._flags.isSeeking) {
        Log.notice('Converting current buffer to seek')

        this._chronos.buffer.startTime = this._chronos.seek.startTime
        this._chronos.buffer.lastTime = 0
        this._chronos.seek.stop()

        this._flags.isSeeking = false
      } else {
        this._chronos.buffer.start()
      }

      this._flags.isBuffering = true
    },

    /**
     * Return the service name
     *
     * @returns {string}
     */
    getServiceName: function () {
      return '/buffer-begin'
    }
  }
)

module.exports = BufferBeginActionBuilder
