var YouboraObject = require('../object')
var Timer = require('../timer')
var Chrono = require('../chrono')
  // var Log = require('../log')

var PlayheadMonitor = YouboraObject.extend(
  /** @lends PlayheadMonitor.prototype */
  {
    /**
     * This Class store youbora configuration settings.
     *
     * @constructs PlayheadMonitor
     * @extends YouboraObject
     * @memberof youbora
     *
     * @param {GenericPlugin} plugin The plugin to monitor. Must have getPlayhead defined.
     * @param {PlayheadMonitor.Type} [type=NONE]
     * Which metric to monitor seek and/or buffer.
     * Use bitwise operators to join both values (Type.BUFFER | Type.SEEK)
     * @param {int} [interval=800] How many ms will wait between progress. -1 to disable.
     */
    constructor: function (plugin, type, interval) {
      this._plugin = plugin
      this._seekEnabled = type & PlayheadMonitor.Type.SEEK
      this._bufferEnabled = type & PlayheadMonitor.Type.BUFFER
      this._interval = interval || 800

      this._chrono = new Chrono()
      this._jumpNextProgress = false
      this._lastPlayhead = 0

      if (this._interval > 0) {
        this._timer = new Timer(this.progress.bind(this), this._interval)
      }

      this._registerListeners()
    },

    /**
     * Start interval checks.
     */
    start: function () {
      this._timer.start()
    },

    /**
     * Stop interval checks.
     */
    stop: function () {
      if (this._timer) this._timer.stop()
    },

    _registerListeners: function () {
      this._plugin.on('resume', this._resumeListener.bind(this))
    },

    _resumeListener: function (e) {
      this._jumpNextProgress = true
    },

    /**
     * Call this method at every tick of timeupdate/progress.
     * If you defined an interval, do not fire this method manually.
     */
    progress: function () {
      if (this._jumpNextProgress) {
        this._jumpNextProgress = false
        return // Jump tick
      }

      // Reset timer
      var deltaTime = this._chrono.stop()
      this._chrono.start()

      // Define thresholds
      var seekThreshold = deltaTime * PlayheadMonitor.kTHRESHOLD_RATIO
      var bufferThreshold = deltaTime / PlayheadMonitor.kTHRESHOLD_RATIO
      var minBuffer = deltaTime * PlayheadMonitor.kMIN_BUFFER_RATIO

      // Calculate diff playhead
      var currentPlayhead = this._getPlayhead()
      var diffPlayhead = Math.abs(this._lastPlayhead - currentPlayhead) * 1000

      // Log.debug('curr: ' + currentPlayhead +
      //   ' last: ' + this._lastPlayhead +
      //   ' diff: ' + diffPlayhead)

      if (diffPlayhead < bufferThreshold) {
        // Playhead is stalling > buffer
        if (this._bufferEnabled &&
          this._lastPlayhead > 0 &&
          !this._plugin.flags.isBuffering &&
          !this._plugin.flags.isPaused &&
          !this._plugin.flags.isSeeking
        ) {
          this._plugin.emit('buffer-begin')
        }
      } else if (diffPlayhead > seekThreshold) {
        // Playhead has jumped > seek
        if (this._seekEnabled && this._lastPlayhead > 0 &&
          !this._plugin.flags.isSeeking
        ) {
          this._plugin.emit('seek-begin')
        }
      } else {
        // Healthy
        if (this._seekEnabled &&
          this._plugin.flags.isSeeking
        ) {
          this._plugin.emit('seek-end')
        } else if (
          this._bufferEnabled &&
          this._plugin.flags.isBuffering &&
          this._plugin.chronos.buffer.getDeltaTime(false) > minBuffer
        ) {
          this._plugin.emit('buffer-end')
        }
      }

      // Update Playhead
      this._lastPlayhead = currentPlayhead
    },
    /**
     * Returns plugin's playhead. Override to add a custom playhead getter.
     * @private
     * @returns {double} Playhead in seconds
     */
    _getPlayhead: function () {
      return this._plugin.getPlayhead()
    }
  }, {
    // Static methods

    /**
     * Enum for monitoring type
     * @enum
     */
    Type: {
      /** Would not monitor */
      NONE: 0,
      /** Sends buffer-begin/end */
      BUFFER: 1,
      /** Sends seek-begin/end */
      SEEK: 2
    },

    /** Threshold multiplier */
    kTHRESHOLD_RATIO: 2,

    /** Ignore small buffers under this ratio */
    kMIN_BUFFER_RATIO: 1.1

  })

module.exports = PlayheadMonitor
