var YouboraObject = require('../object')

var FlagStatus = YouboraObject.extend(
  /** @lends FlagStatus.prototype */
  {
    /**
     * This class contains all the flags related to view status.
     * Each plugin will have an instance of this class.
     *
     * @constructs FlagStatus
     * @extends YouboraObject
     * @memberof youbora
     */
    constructor: function () {
      this.reset()
    },

    /** Reset flag values */
    reset: function () {
      /** Flag when player is preloading. */
      this.isPreloading = false

      /** Flag when Init has been sent. */
      this.isInitiated = false

      /** Flag when Start has been sent. */
      this.isStarted = false

      /** Flag when Join has been sent. */
      this.isJoined = false

      /** Flag when Player is paused. */
      this.isPaused = false

      /** Flag when Player is seeking. */
      this.isSeeking = false

      /** Flag when Player is buffering. */
      this.isBuffering = false

      /** Flag trued when a fatal error is thrown */
      this.isHalted = false
    }
  }
)

module.exports = FlagStatus
