var ActionBuilder = require('./actionbuilder')
var StartActionBuilder = require('./start')
var Log = require('../log')

/**
 * This class will build request, manipulate flags and chronos for the associated event.
 *
 * @constructs ErrorActionBuilder
 * @extends ActionBuilder
 * @memberof youbora
 * @abstract
 */
var ErrorActionBuilder = ActionBuilder.extend(
  /** @lends ErrorActionBuilder.prototype */
  {
    /**
     * Return the service name
     *
     * @returns {string}
     */
    getServiceName: function () {
      return '/error'
    },

    /**
     * Logs successful request.
     *
     * @param {object} params Object of params created.
     */
    notice: function (params) {
      Log.notice(this.getServiceName() + ' ' + params.errorMsg)
    },

    /**
     * Return the params needed for the request.
     *
     * @returns {object}
     */
    getParams: function (params) {
      params = new StartActionBuilder(this._info).getParams(params)
      return this._buildParams(params, ['player'])
    }
  }
)

module.exports = ErrorActionBuilder
