var ActionBuilder = require('./actionbuilder')

/**
 * This class will build request, manipulate flags and chronos for the associated event.
 *
 * @constructs PreloadBeginActionBuilder
 * @extends ActionBuilder
 * @memberof youbora
 * @abstract
 */
var PreloadBeginActionBuilder = ActionBuilder.extend(
  /** @lends PreloadBeginActionBuilder.prototype */
  {
    /**
     * Check the associated flags. ie: return this._info.getIsPreloadBeginSent()
     *
     * @returns {boolean}
     */
    getIsAllowed: function () {
      return !this._flags.isPreloading
    },

    /**
     *update chronos and flags as needed.
     * ie: this._info.plugin.chronos.total.start()
     */
    setChronosAndFlags: function () {
      this._flags.isPreloading = true

      this._chronos.preload.start()
    },

    /**
     * Return the service name
     *
     * @returns {string}
     */
    getServiceName: function () {
      return '/preload-begin'
    }
  }
)

module.exports = PreloadBeginActionBuilder
