var YouboraObject = require('../object')
var Log = require('../log')
var Util = require('../util')
var version = require('../version')

var InfoGatherer = YouboraObject.extend(
  /** @lends InfoGatherer.prototype */
  {
    /**
     * This class will fetch information about the reproduction from a {@link YouboraController}
     *
     * @constructs InfoGatherer
     * @extends YouboraObject
     * @memberof youbora
     *
     * @param {YouboraController} controller A YouboraController instance
     */
    constructor: function (controller) {
      this.plugin = controller.plugin
      this._options = controller.options
      this._controller = controller

      /** Stores last sent values */
      this.lastSent = {}
    },

    // ------------------------------------- INFO GETTERS -----------------------------------------

    /** Returns service host */
    getHost: function () {
      return Util.addProtocol(Util.stripProtocol(
        this._options['config.host'],
        this._options['config.httpSecure']
      ))
    },

    /** Returns content's playhead */
    getPlayhead: function () {
      var ret = 0
      try {
        ret = this.plugin.getPlayhead()
      } catch (err) {
        Log.warn('An error occurred while calling getPlayhead', err)
      }
      return Util.parseNumber(ret, 0)
    },

    /** Returns content's PlayRate */
    getPlayrate: function () {
      var ret = ''
      try {
        ret = this.plugin.getPlayrate()
      } catch (err) {
        Log.warn('An error occured while calling getPlayrate', err)
      }
      return ret
    },

    /** Returns content's FramesPerSecond */
    getFramesPerSecond: function () {
      var ret = ''
      try {
        ret = this.plugin.getFramesPerSecond()
      } catch (err) {
        Log.warn('An error occured while calling getFramesPerSecond', err)
      }
      return ret
    },

    /** Returns content's DroppedFrames */
    getDroppedFrames: function () {
      var ret = ''
      try {
        ret = this.plugin.getDroppedFrames()
        if (!ret) {
          ret = this.getWebkitDroppedFrames()
        }
      } catch (err) {
        Log.warn('An error occured while calling getDroppedFrames', err)
      }
      return Util.parseNumber(ret, 0)
    },

    /**
     * Returns dropped frames as per webkitDroppedFrameCount
     *
     * @returns {number}
     */
    getWebkitDroppedFrames: function () {
      if (this.plugin.tag && this.plugin.tag.webkitDroppedFrameCount) {
        return this.plugin.tag.webkitDroppedFrameCount
      }
      return null
    },

    /** Returns content's Duration */
    getDuration: function () {
      var ret = this._options['content.duration']
      if (ret === null) {
        try {
          ret = this.plugin.getDuration()
        } catch (err) {
          Log.warn('An error occured while calling getDuration', err)
        }
      }
      return Util.parseNumber(Math.round(ret), 0)
    },

    /** Returns content's Bitrate */
    getBitrate: function () {
      var ret = this._options['content.bitrate']
      if (ret === null) {
        try {
          ret = this.plugin.getBitrate()
        } catch (err) {
          Log.warn('An error occured while calling getBitrate', err)
        }

        if (!ret || ret === -1) {
          ret = this.getWebkitBitrate()
        }
      }
      return Util.parseNumber(ret, -1)
    },

    /**
     * Returns bitrate as per webkitVideoDecodedByteCount
     *
     * @param {Object} tag Video tag DOM reference.
     * @returns {number}
     */
    getWebkitBitrate: function () {
      if (this.plugin.tag && this.plugin.tag.webkitVideoDecodedByteCount) {
        var bitrate = this.plugin.tag.webkitVideoDecodedByteCount
        if (this._lastAdBitrate) {
          var delta = this.plugin.tag.webkitVideoDecodedByteCount - this._lastAdBitrate
          bitrate = Math.round(((delta) / 5) * 8)
        }
        this._lastAdBitrate = this.plugin.tag.webkitVideoDecodedByteCount
        return bitrate !== 0 ? bitrate : -1
      }
    },

    /** Returns content's Throughput */
    getThroughput: function () {
      var ret = this._options['content.throughput']
      if (ret === null) {
        try {
          ret = this.plugin.getThroughput()
        } catch (err) {
          Log.warn('An error occured while calling getThroughput', err)
        }
      }
      return Util.parseNumber(ret, -1)
    },

    /** Returns content's Rendition */
    getRendition: function () {
      var ret = this._options['content.rendition']
      if (ret === null) {
        try {
          ret = this.plugin.getRendition()
        } catch (err) {
          Log.warn('An error occured while calling getRendition', err)
        }
      }
      return ret
    },

    /** Returns content's Title */
    getTitle: function () {
      var ret = this._options['content.title']
      if (ret === null) {
        try {
          ret = this.plugin.getTitle()
        } catch (err) {
          Log.warn('An error occured while calling getTitle', err)
        }
      }
      return ret
    },

    /** Returns content's Title2 */
    getTitle2: function () {
      var ret = this._options['content.title2']
      if (ret === null) {
        try {
          ret = this.plugin.getTitle2()
        } catch (err) {
          Log.warn('An error occured while calling getTitle2', err)
        }
      }
      return ret
    },

    /** Returns content's IsLive */
    getIsLive: function () {
      var ret = this._options['content.isLive']
      if (ret === null) {
        try {
          ret = this.plugin.getIsLive()
        } catch (err) {
          Log.warn('An error occured while calling getIsLive', err)
        }
      }
      return ret
    },

    /** Returns content's Resource after being parsed by the resourceTransform */
    getResource: function () {
      var ret = null
      if (!this._controller.resourceTransform.isBlocking()) {
        ret = this._controller.resourceTransform.getResource()
      }

      return ret || this.getOriginalResource()
    },

    /** Returns content's original Resource */
    getOriginalResource: function () {
      var ret = null
      ret = this._options['content.resource']
      if (ret === null) {
        try {
          ret = this.plugin.getResource()
        } catch (err) {
          Log.warn('An error occured while calling getResource', err)
          ret = null
        }
      }
      return ret || 'unknown'
    },

    /** Returns content's TransactionCode */
    getTransactionCode: function () {
      return this._options['content.transactionCode']
    },

    /** Returns content's Metadata */
    getMetadata: function () {
      return this._options['content.metadata']
    },

    /** Returns content's PlayerVersion */
    getPlayerVersion: function () {
      var ret = ''
      try {
        ret = this.plugin.getPlayerVersion()
      } catch (err) {
        Log.warn('An error occured while calling getPlayerVersion', err)
      }
      return ret
    },

    /** Returns content's PlayerName */
    getPlayerName: function () {
      var ret = ''
      try {
        ret = this.plugin.getPlayerName()
      } catch (err) {
        Log.warn('An error occured while calling getPlayerName', err)
      }
      return ret
    },

    /** Returns content's Cdn */
    getCdn: function () {
      var ret = null
      if (!this._controller.resourceTransform.isBlocking()) {
        ret = this._controller.resourceTransform.getCdnName()
      }
      return ret || this._options['content.cdn']
    },

    /** Returns content's PluginVersion */
    getPluginVersion: function () {
      var ret = ''
      try {
        ret = this.plugin.getVersion()
      } catch (err) {
        Log.warn('An error occured while calling getPluginVersion', err)
      }
      return ret
    },

    /** Returns content's Extraparam1 */
    getExtraparam1: function () {
      return this._options['extraparam.1']
    },

    /** Returns content's Extraparam2 */
    getExtraparam2: function () {
      return this._options['extraparam.2']
    },

    /** Returns content's Extraparam3 */
    getExtraparam3: function () {
      return this._options['extraparam.3']
    },

    /** Returns content's Extraparam4 */
    getExtraparam4: function () {
      return this._options['extraparam.4']
    },
    /** Returns content's Extraparam5 */
    getExtraparam5: function () {
      return this._options['extraparam.5']
    },

    /** Returns content's Extraparam6 */
    getExtraparam6: function () {
      return this._options['extraparam.6']
    },

    /** Returns content's Extraparam7 */
    getExtraparam7: function () {
      return this._options['extraparam.7']
    },

    /** Returns content's Extraparam8 */
    getExtraparam8: function () {
      return this._options['extraparam.8']
    },

    /** Returns content's Extraparam9 */
    getExtraparam9: function () {
      return this._options['extraparam.9']
    },

    /** Returns content's Extraparam10 */
    getExtraparam10: function () {
      return this._options['extraparam.10']
    },

    /** Returns ad's position */
    getAdPosition: function () {
      var ret = null
      if (this.plugin.adnalyzer) {
        try {
          ret = this.plugin.adnalyzer.getPosition()
        } catch (err) {
          Log.warn('An error occured while calling getAdPosition', err)
        }
      }
      return ret
    },

    /** Returns ad's AdPlayhead */
    getAdPlayhead: function () {
      var ret = null
      if (this.plugin.adnalyzer) {
        try {
          ret = this.plugin.adnalyzer.getPlayhead()
        } catch (err) {
          Log.warn('An error occured while calling getAdPlayhead', err)
        }
      }
      return Util.parseNumber(ret, 0)
    },

    /** Returns ad's AdDuration */
    getAdDuration: function () {
      var ret = null
      if (this.plugin.adnalyzer) {
        try {
          ret = this.plugin.adnalyzer.getDuration()
        } catch (err) {
          Log.warn('An error occured while calling getAdDuration', err)
        }
      }
      return Util.parseNumber(ret, 0)
    },

    /** Returns ad's AdBitrate */
    getAdBitrate: function () {
      var ret = null
      if (this.plugin.adnalyzer) {
        try {
          ret = this.plugin.adnalyzer.getBitrate()
        } catch (err) {
          Log.warn('An error occured while calling getAdBitrate', err)
        }

        if (!ret || ret === -1) {
          ret = this.getWebkitAdBitrate()
        }
      }
      return Util.parseNumber(ret, -1)
    },

    /**
     * Returns bitrate as per webkitVideoDecodedByteCount
     *
     * @param {Object} tag Video tag DOM reference.
     * @returns {number}
     */
    getWebkitAdBitrate: function () {
      if (this.plugin.tag && this.plugin.tag.webkitVideoDecodedByteCount) {
        var bitrate = this.plugin.tag.webkitVideoDecodedByteCount
        if (this._lastAdBitrate) {
          var delta = this.plugin.tag.webkitVideoDecodedByteCount - this._lastAdBitrate
          bitrate = Math.round(((delta) / 5) * 8)
        }
        this._lastAdBitrate = this.plugin.tag.webkitVideoDecodedByteCount
        return bitrate !== 0 ? bitrate : -1
      }
    },

    /** Returns ad's AdTitle */
    getAdTitle: function () {
      var ret = null
      if (this.plugin.adnalyzer) {
        try {
          ret = this.plugin.adnalyzer.getTitle()
        } catch (err) {
          Log.warn('An error occured while calling getAdTitle', err)
        }
      }
      return ret
    },

    /** Returns ad's AdResource */
    getAdResource: function () {
      var ret = null
      if (this.plugin.adnalyzer) {
        try {
          ret = this.plugin.adnalyzer.getResource()
        } catch (err) {
          Log.warn('An error occured while calling getAdResource', err)
        }
      }
      return ret
    },

    /** Returns ad's AdPlayerVersion */
    getAdPlayerVersion: function () {
      var ret = null
      if (this.plugin.adnalyzer) {
        try {
          ret = this.plugin.adnalyzer.getPlayerVersion()
        } catch (err) {
          Log.warn('An error occured while calling getAdPlayerVersion', err)
        }
      }
      return ret
    },

    /** Returns ad's AdMetadata */
    getAdMetadata: function () {
      return this._options['ad.metadata']
    },

    /** Returns ad's AdnalyzerVersion */
    getAdnalyzerVersion: function () {
      var ret = null
      if (this.plugin.adnalyzer) {
        try {
          ret = this.plugin.adnalyzer.getVersion()
        } catch (err) {
          Log.warn('An error occured while calling getAdnalyzerVersion', err)
        }
      }
      return ret
    },

    /** Returns PluginInfo */
    getPluginInfo: function () {
      var ret = {
        pluginVersion: this.getPluginVersion(),
        libVersion: version,
        adnalyzerVersion: this.getAdnalyzerVersion()
      }
      return ret
    },

    /** Returns Ip */
    getIp: function () {
      return this._options['network.ip']
    },

    /** Returns Isp */
    getIsp: function () {
      return this._options['network.isp']
    },

    /** Returns ConnectionType */
    getConnectionType: function () {
      return this._options['network.connectionType']
    },

    /** Returns DeviceCode */
    getDeviceCode: function () {
      return this._options['device.code']
    },

    /** Returns AccountCode */
    getAccountCode: function () {
      return this._options['config.accountCode']
    },

    /** Returns Username */
    getUsername: function () {
      return this._options['config.username']
    },

    /** Get URL referer. */
    getReferer: function () {
      var ret = ''
      if (typeof window !== 'undefined' && window.location) {
        ret = window.location.href
      }
      return ret
    },

    /**
     * Returns the nodehost
     */
    getNodeHost: function () {
      return this._controller.resourceTransform.getNodeHost()
    },

    /**
     * Returns the node type id
     */
    getNodeType: function () {
      return this._controller.resourceTransform.getNodeType()
    },

    /**
     * Returns the node type string
     */
    getNodeTypeString: function () {
      return this._controller.resourceTransform.getNodeTypeString()
    },

    // ---------------------------------------- CHRONOS -------------------------------------------

    /** Returns JoinDuration chrono delta time */
    getJoinDuration: function () {
      return this.plugin.chronos.join.getDeltaTime(false)
    },

    /** Returns BufferDuration chrono delta time */
    getBufferDuration: function () {
      return this.plugin.chronos.buffer.getDeltaTime(false)
    },

    /** Returns SeekDuration chrono delta time */
    getSeekDuration: function () {
      return this.plugin.chronos.seek.getDeltaTime(false)
    },

    /** Returns pauseDuration chrono delta time */
    getPauseDuration: function () {
      return this.plugin.chronos.pause.getDeltaTime(false)
    },

    /** Returns preload chrono delta time */
    getPreloadDuration: function () {
      return this.plugin.chronos.preload.getDeltaTime(false)
    },

    /** Returns AdJoinDuration chrono delta time */
    getAdJoinDuration: function () {
      return this.plugin.adnalyzer ? this.plugin.adnalyzer.chronos.join.getDeltaTime(false) : -1
    },

    /** Returns AdBufferDuration chrono delta time */
    getAdBufferDuration: function () {
      return this.plugin.adnalyzer ? this.plugin.adnalyzer.chronos.buffer.getDeltaTime(false) : -1
    },

    /** Returns AdPauseDuration chrono delta time */
    getAdPauseDuration: function () {
      return this.plugin.adnalyzer ? this.plugin.adnalyzer.chronos.pause.getDeltaTime(false) : -1
    },

    /** Returns total totalAdDuration chrono delta time */
    getTotalAdDuration: function () {
      return this.plugin.adnalyzer ? this.plugin.adnalyzer.chronos.total.getDeltaTime(false) : -1
    }
  }
)

module.exports = InfoGatherer
