var ActionBuilder = require('./actionbuilder')
var Log = require('../log')

/**
 * This class will build request, manipulate flags and chronos for the associated event.
 *
 * @constructs DataActionBuilder
 * @extends ActionBuilder
 * @memberof youbora
 * @abstract
 */
var DataActionBuilder = ActionBuilder.extend(
  /** @lends DataActionBuilder.prototype */
  {
    /**
     * Return the service name
     *
     * @returns {string}
     */
    getServiceName: function () {
      return '/data'
    },

    /**
     * Logs successful request.
     *
     * @param {object} params Object of params created.
     */
    notice: function (params) {
      Log.notice(this.getServiceName() + ' ' + params.system)
    },

    /**
     * Return the params needed for the request.
     *
     * @returns {object}
     */
    getParams: function (params) {
      params = this._buildParams(params, ['pluginVersion', 'system'])
      params.apiVersion = 'v6,v7'
      params.outputformat = 'jsonp'
      return params
    }
  }
)

module.exports = DataActionBuilder
