var ActionBuilder = require('./actionbuilder')
var Log = require('../log')

/**
 * This class will build request, manipulate flags and chronos for the associated event.
 *
 * @constructs SeekBeginActionBuilder
 * @extends ActionBuilder
 * @memberof youbora
 * @abstract
 */
var SeekBeginActionBuilder = ActionBuilder.extend(
  /** @lends SeekBeginActionBuilder.prototype */
  {

    /**
     * Check the associated flags. ie: return this._info.getIsSeekBeginSent()
     *
     * @returns {boolean}
     */
    getIsAllowed: function () {
      return !this._flags.isHalted &&
        this._flags.isJoined &&
        !this._flags.isSeeking
    },

    /**
     *update chronos and flags as needed.
     * Will convert from buffer if e.data.convertFromSeek is NOT false.
     * ie: this._info.plugin.chronos.total.start()
     *
     * @param {Object} [e] The event info
     */
    setChronosAndFlags: function (e) {
      if (e.data && e.data.convertFromBuffer !== false && this._flags.isBuffering) {
        Log.notice('Converting current buffer to seek')

        this._chronos.seek.startTime = this._chronos.buffer.startTime
        this._chronos.seek.lastTime = 0
        this._chronos.buffer.stop()

        this._flags.isBuffering = false
      } else {
        this._chronos.seek.start()
      }

      this._flags.isSeeking = true
    },

    /**
     * Return the service name
     *
     * @returns {string}
     */
    getServiceName: function () {
      return '/seek-begin'
    }
  }
)

module.exports = SeekBeginActionBuilder
