var Transform = require('./transform')
var HlsTransform = require('./hls')
var CdnTransform = require('./cdn')
var Log = require('../log')

var ResourceTransform = Transform.extend(
  /** @lends ResourceTransform.prototype */
  {
    /**
     * This class parses resource to fetch transportstreams, CDNs and Node info.
     *
     * @constructs ResourceTransform
     * @extends Transform
     * @memberof youbora
     *
     * @param {Otions} options Instance of {@link Options}
     */
    constructor: function (options, info) {
      ResourceTransform.__super__.constructor.apply(this, arguments)

      this._hlsEnabled = options['parse.HLS']
      this._cdnEnabled = options['parse.CDNNode']
      this._cdnList = options['parse.CDNNode.list']
      this._cdnNameHeader = options['parse.CDNNameHeader']
      CdnTransform.setBalancerHeaderName(this._cdnNameHeader)
      this._info = info

      this._isBusy = false
    },

    getResource: function () {
      return this._realResource || this._initResource
    },

    getCdnName: function () {
      return this._cdnName
    },

    getNodeHost: function () {
      return this._cdnNodeHost
    },

    getNodeTypeString: function () {
      return this._cdnNodeTypeString
    },

    getNodeType: function () {
      return this._cdnNodeType
    },

    init: function (resource) {
      if (!this._isBusy) {
        this._isBusy = true

        this._initResource = resource

        this._setTimeout()

        if (this._hlsEnabled) {
          this._parseHLS()
        } else if (this._cdnEnabled) {
          this._parseCDN()
        } else {
          this.done()
        }
      }
    },

    _setTimeout: function () {
      // Abort operation after 3s
      this._parseTimeout = setTimeout(function () {
        if (this._isBusy) {
          this.done()
          Log.warn(
            'ResourceTransform has exceded the maximum execution time (3s) and will be aborted.'
          )
        }
      }.bind(this), 3000)
    },

    _parseHLS: function () {
      var hlsTransform = new HlsTransform()
      hlsTransform.on(HlsTransform.Event.DONE, function () {
        this._realResource = this._hlsTransform.getResource()
        if (this._cdnEnabled) {
          this._parseCDN()
        } else {
          this.done()
        }
      }.bind(this))
      hlsTransform.parse(this._initResource)
    },

    _parseCDN: function () {
      var cdnList = this._cdnList.slice()
      cdnList.forEach(function (cdn) {
        if (this.getNodeHost()) return // break

        var cdnTransform = CdnTransform.create(cdn)
        cdnTransform.on(CdnTransform.Event.DONE, function () {
          this._cdnName = cdnTransform.getCdnName()
          this._cdnNodeHost = cdnTransform.getNodeHost()
          this._cdnNodeTypeString = cdnTransform.getNodeTypeString()
          this._cdnNodeType = cdnTransform.getNodeType()

          if (this.getNodeHost()) this.done()
        }.bind(this))
        cdnTransform.parse(this.getResource())
      }.bind(this))
    },

    /**
     * Transform requests
     * @param {YBRequest} request YBRequest to transform.
     */
    parse: function (request) {
      if (request.service === '/start') {
        this._info.lastSent.mediaResource = request.params.mediaResource = this.getResource()

        if (this._cdnEnabled) {
          this._info.lastSent.cdn = request.params.cdn = request.params.cdn || this.getCdnName()
          this._info.lastSent.nodeHost = request.params.nodeHost = this.getNodeHost()
          this._info.lastSent.nodeType = request.params.nodeType = this.getNodeType()
          request.params.nodeTypeString = this.getNodeTypeString()
          this._info.lastSent.nodeTypeString = request.params.nodeTypeString
        }
      }
    }
  })

module.exports = ResourceTransform
