var YouboraObject = require('../object')
var Log = require('../log')
var YBRequest = require('../comm/request')
var FlagStatus = require('../plugin/flagstatus')
var ChronoStatus = require('../plugin/chronostatus')

var ActionBuilder = YouboraObject.extend(
  /** @lends ActionBuilder.prototype */
  {
    /**
     * This class will build params associated with each event: /start, /joinTime...
     *
     * @constructs ActionBuilder
     * @extends YouboraObject
     * @memberof youbora
     * @abstract
     *
     * @param {InfoManager} gatherer An InfoManager instance
     */
    constructor: function (infomanager) {
      this._info = infomanager

      this._monitor = infomanager.plugin.monitor
      this._flags = infomanager.plugin.flags
      this._chronos = infomanager.plugin.chronos

      if (infomanager.plugin.adnalyzer) {
        this._adFlags = infomanager.plugin.adnalyzer.flags
        this._adChronos = infomanager.plugin.adnalyzer.chronos
        this._adMonitor = infomanager.plugin.adnalyzer._adMonitor
      } else {
        this._adFlags = new FlagStatus()
        this._adChronos = new ChronoStatus()
      }
    },

    /**
     * Override this method to check the associated flags. ie: return this._info.getIsStartSent()
     */
    getIsAllowed: function () {
      return true
    },

    /**
     * Override this method to update chronos and flags as needed.
     * ie: this._info.plugin.chronos.joinDuration.start()
     *
     * @returns {boolean}
     */
    setChronosAndFlags: function () {},

    /**
     * Override this method to return the service name. ie: /start, /joinTime...
     *
     * @returns {string}
     */
    getServiceName: function () {
      return '/'
    },

    /**
     * Override this method to return the params needed for the request.
     *
     * @returns {object}
     */
    getParams: function () {
      return {}
    },

    /**
     * Notices when the request is succesfully generated. Override this for custom logs
     *
     * @param {object} params Object of params created.
     */
    notice: function (req) {
      Log.notice(this.getServiceName())
    },

    /**
     * Returns a request ready to be enqueued using {@link Communication#sendRequest}
     *
     * @param {object} [params] Params to be sent to getParams function.
     * @return {YBRequest}
     */
    buildRequest: function (params) {
      var request = null

      if (this.getIsAllowed()) {
        this.setChronosAndFlags()

        params = this.getParams(params)
        var service = this.getServiceName()

        request = new YBRequest(null, service, params)

        this.notice(params)
      }

      return request
    },

    /**
     * Adds to params all the entities specified in paramList, unless they are already set.
     *
     * @private
     * @param {Object} params Object of params key:value.
     * @param {Array[string]} paramList A list of params to fetch.
     */
    _buildParams: function (params, paramList) {
      params = params || {}
      paramList.forEach(function (param) {
        if (params[param]) { return }
        var getterName = ActionBuilder.Param[param]

        if (this._info[getterName]) {
          params[param] = this._info[getterName]()
          this._info.lastSent[param] = params[param]
        } else {
          Log.warn('Trying to call undefined getter ' + param + ':' + getterName)
        }
      }.bind(this))
      return params
    },

    /**
     * Similar to _buildParams but only adds params if they have changed.
     *
     * @private
     * @param {Object} params Object of params key:value.
     * @param {Array[string]} paramList A list of params to fetch.
     */
    _buildParamsIfDifferent: function (params, paramList) {
      params = params || {}
      paramList.forEach(function (param) {
        if (params[param]) { return }
        var getterName = ActionBuilder.Param[param]

        if (this._info[getterName]) {
          var value = this._info[getterName]()
          if (this._info.lastSent[param] !== value && value) {
            params[param] = value
            this._info.lastSent[param] = value
          }
        } else {
          Log.warn('Trying to call undefined getter ' + param + ':' + getterName)
        }
      }.bind(this))
      return params
    }
  }, {
    // Static Members
    /** List of params and its related getter */
    Param: {
      playhead: 'getPlayhead',
      playrate: 'getPlayrate',
      fps: 'getFramesPerSecond',
      droppedFrames: 'getDroppedFrames',
      mediaDuration: 'getDuration',
      bitrate: 'getBitrate',
      throughput: 'getThroughput',
      rendition: 'getRendition',
      title: 'getTitle',
      title2: 'getTitle2',
      live: 'getIsLive',
      mediaResource: 'getResource',
      transactionCode: 'getTransactionCode',
      properties: 'getMetadata',
      playerVersion: 'getPlayerVersion',
      player: 'getPlayerName',
      cdn: 'getCdn',
      pluginVersion: 'getPluginVersion',

      param1: 'getExtraparam1',
      param2: 'getExtraparam2',
      param3: 'getExtraparam3',
      param4: 'getExtraparam4',
      param5: 'getExtraparam5',
      param6: 'getExtraparam6',
      param7: 'getExtraparam7',
      param8: 'getExtraparam8',
      param9: 'getExtraparam9',
      param10: 'getExtraparam10',

      adPosition: 'getAdPosition',
      adPlayhead: 'getAdPlayhead',
      adDuration: 'getAdDuration',
      adBitrate: 'getAdBitrate',
      adTitle: 'getAdTitle',
      adResource: 'getAdResource',
      adPlayerVersion: 'getAdPlayerVersion',
      adProperties: 'getAdMetadata',
      adnalyzerVersion: 'getAdnalyzerVersion',

      pluginInfo: 'getPluginInfo',

      isp: 'getIsp',
      connectionType: 'getConnectionType',
      ip: 'getIp',

      deviceCode: 'getDeviceCode',

      system: 'getAccountCode',
      accountCode: 'getAccountCode',
      username: 'getUsername',

      joinDuration: 'getJoinDuration',
      bufferDuration: 'getBufferDuration',
      seekDuration: 'getSeekDuration',
      pauseDuration: 'getPauseDuration',

      adJoinDuration: 'getAdJoinDuration',
      adBufferDuration: 'getAdBufferDuration',
      totalAdDuration: 'getTotalAdDuration',
      preloadDuration: 'getPreloadDuration',
      adPauseDuration: 'getAdPauseDuration',

      referer: 'getReferer',

      nodeHost: 'getNodeHost',
      nodeType: 'getNodeType',
      nodeTypeString: 'getNodeTypeString'
    }

  }
)

module.exports = ActionBuilder
