var ActionBuilder = require('./actionbuilder')
var Log = require('../log')

/**
 * This class will build request, manipulate flags and chronos for the associated event.
 *
 * @constructs BufferEndActionBuilder
 * @extends ActionBuilder
 * @memberof youbora
 * @abstract
 */
var BufferEndActionBuilder = ActionBuilder.extend(
  /** @lends BufferEndActionBuilder.prototype */
  {

    /**
     * Check the associated flags. ie: return this._info.getIsBufferEndSent()
     *
     * @returns {boolean}
     */
    getIsAllowed: function () {
      return !this._flags.isHalted &&
        this._flags.isJoined &&
        this._flags.isBuffering
    },

    /**
     *update chronos and flags as needed.
     * ie: this._info.plugin.chronos.total.start()
     */
    setChronosAndFlags: function () {
      this._flags.isBuffering = false

      this._chronos.buffer.stop()
    },

    /**
     * Return the service name
     *
     * @returns {string}
     */
    getServiceName: function () {
      return '/bufferUnderrun'
    },

    /**
     * Logs successful request.
     *
     * @param {object} params Object of params created.
     */
    notice: function (params) {
      Log.notice(this.getServiceName() + ' ' + params.bufferDuration + 'ms')
    },

    /**
     * Return the params needed for the request.
     *
     * @returns {object}
     */
    getParams: function (params) {
      return this._buildParams(params, ['bufferDuration', 'playhead'])
    }
  }
)

module.exports = BufferEndActionBuilder
