var ActionBuilder = require('./actionbuilder')

/**
 * This class will build request, manipulate flags and chronos for the associated event.
 *
 * @constructs AdBufferBeginActionBuilder
 * @extends ActionBuilder
 * @memberof youbora
 * @abstract
 */
var AdBufferBeginActionBuilder = ActionBuilder.extend(
  /** @lends AdBufferBeginActionBuilder.prototype */
  {
    /**
     * Check the associated flags. ie: return this._info.getIsAdBufferBeginSent()
     *
     * @returns {boolean}
     */
    getIsAllowed: function () {
      return !this._flags.isHalted &&
        this._adFlags.isJoined &&
        !this._adFlags.isBuffering
    },

    /**
     *update chronos and flags as needed.
     * ie: this._info.plugin.chronos.total.start()
     */
    setChronosAndFlags: function () {
      this._adFlags.isBuffering = true

      this._adChronos.buffer.start()
    },

    /**
     * Return the service name
     *
     * @returns {string}
     */
    getServiceName: function () {
      return '/adBuffer-begin'
    }
  }
)

module.exports = AdBufferBeginActionBuilder
