var ActionBuilder = require('./actionbuilder')
var Log = require('../log')

/**
 * This class will build request, manipulate flags and chronos for the associated event.
 *
 * @constructs JoinActionBuilder
 * @extends ActionBuilder
 * @memberof youbora
 * @abstract
 */
var JoinActionBuilder = ActionBuilder.extend(
  /** @lends JoinActionBuilder.prototype */
  {

    /**
     * Check the associated flags. ie: return this._info.getIsJoinSent()
     *
     * @returns {boolean}
     */
    getIsAllowed: function () {
      return !this._flags.isHalted &&
        this._flags.isStarted &&
        !this._flags.isJoined &&
        !this._adFlags.isStarted
    },

    /**
     *update chronos and flags as needed.
     * ie: this._info.plugin.chronos.join.start()
     */
    setChronosAndFlags: function () {
      this._flags.isJoined = true

      this._chronos.join.stop()
    },

    /**
     * Return the service name
     *
     * @returns {string}
     */
    getServiceName: function () {
      return '/joinTime'
    },

    /**
     * Logs successful request.
     *
     * @param {object} params Object of params created.
     */
    notice: function (params) {
      Log.notice(this.getServiceName() + ' ' + params.joinDuration + 'ms')
    },

    /**
     * Return the params needed for the request.
     *
     * @returns {object}
     */
    getParams: function (params) {
      params = this._buildParams(params, ['joinDuration', 'playhead'])
      return this._buildParamsIfDifferent(params, [
        'title',
        'title2',
        'live',
        'mediaDuration',
        'mediaResource'
      ])
    }
  }
)

module.exports = JoinActionBuilder
