var ActionBuilder = require('./actionbuilder')
var Log = require('../log')

/**
 * This class will build request, manipulate flags and chronos for the associated event.
 *
 * @constructs StopActionBuilder
 * @extends ActionBuilder
 * @memberof youbora
 * @abstract
 */
var StopActionBuilder = ActionBuilder.extend(
  /** @lends StopActionBuilder.prototype */
  {
    /**
     * Check the associated flags. ie: return this._info.getIsResumeSent()
     *
     * @returns {boolean}
     */
    getIsAllowed: function () {
      return !this._flags.isHalted &&
        this._flags.isStarted
    },

    /**
     *update chronos and flags as needed.
     * ie: this._info.plugin.chronos.total.start()
     */
    setChronosAndFlags: function () {
      var chrono = this._chronos.total
      chrono.stop()

      this._flags.reset()
      this._adFlags.reset()
      this._chronos.reset()
      this._adChronos.reset()

      this._chronos.total = chrono

      if (this._monitor) this._monitor.stop()
    },

    /**
     * Return the service name
     *
     * @returns {string}
     */
    getServiceName: function () {
      return '/stop'
    },

    /**
     * Logs successful request.
     *
     * @param {object} params Object of params created.
     */
    notice: function (params) {
      Log.notice(this.getServiceName() + ' at ' + params.playhead + 's')
    },

    /**
     * Return the params needed for the request.
     *
     * @returns {object}
     */
    getParams: function (params) {
      return this._buildParams(params, ['bitrate', 'playhead'])
    }
  }
)

module.exports = StopActionBuilder
